import React from 'react';
import { Row, Col, Dropdown, Menu, Space, Modal, Input, message } from 'antd';
import { UserOutlined, DownOutlined, ShareAltOutlined, SyncOutlined , TeamOutlined } from '@ant-design/icons';
import { useSocket } from 'src/providers/SocketProvider';

import './styles.scss';
interface HeaderProps {
    title: string
    showHeaderRight?: boolean
}

function MenuList() {
    const [isModalVisible, setIsModalVisible] = React.useState(false);
    const [playerName, setPlayerName] = React.useState('');
    const socket = useSocket();

    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        setIsModalVisible(false);
        const player = JSON.parse(localStorage.getItem('player') || '{}');
        socket.ws.emit('CHANGE_PLAYER_NAME', { ...player, name: playerName });
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };

    const handleChangeName = (e: any) => {
        setPlayerName(e.target.value);
    }

    React.useEffect(() => {
        const player = JSON.parse(localStorage.getItem('player') || '{}');
        setPlayerName(player.name)
    }, []);

    const handleInvitePlayers = () => {
        navigator.clipboard.writeText(window.location.href);
        message.success('Invitation link copied to clipboard!');
    }

    return (
        <>
            <Menu
                items={[
                    {
                        label: <span onClick={showModal}><UserOutlined /> User Setting</span>,
                        key: '1',
                    },
                    {
                        label: <span onClick={handleInvitePlayers}><ShareAltOutlined /> Invite Players</span>,
                        key: '2',
                    },
                    // {
                    //     label: <span onClick={() => alert('In Development')}><TeamOutlined /> Room Setting</span>,
                    //     key: '3',
                    // },
                ]}
            />
            <Modal title="User Setting" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
                <Input maxLength={14} onChange={handleChangeName} value={playerName} prefix={<UserOutlined />} placeholder='Name' defaultValue="John" />
            </Modal>
        </>
    )
}


const Header = (props: HeaderProps) => (
    <>
        <Row justify='center'>
            <Col xs={22} >
                <div className='headerWrapper'>
                    <div className='headerLeft'>
                        <a href='/'><img width={48} src='/weareplanning.png' /></a>
                        <h1>{props.title}</h1>
                    </div>
                    {
                        props.showHeaderRight? ( <div className='headerRight'>
                            <Dropdown overlay={<MenuList />} trigger={['click']}>
                                <a onClick={e => e.preventDefault()}>
                                    <Space>
                                        <SyncOutlined spin  />
                                        Options
                                        <DownOutlined />
                                    </Space>
                                </a>
                            </Dropdown>,
                        </div> ) : null 
                    }
                </div>
            </Col>
        </Row>
    </>
);

Header.defaultProps = {
    title: '',
    showHeaderRight: false
}

export default Header;