import React from 'react'
import {
  Button,
  Col,
  Row
} from 'antd';
import { useNavigate } from 'react-router-dom';

import './styles.scss';

export default function Main() {
  const navigate = useNavigate();

  const handleCreateGame = () => {
    navigate('/new-game');
  }
  
  return (
    <div className='mainWrapper'>
      <div>
        <Row className='row'>
          <Col xs={24} md={12}>
            <img className='banner' src='/banner.webp' />
          </Col>
        </Row>
        <Row className='row'>
          <Col span={24}>
            <h1 id='title'>We are planning</h1>
          </Col>
        </Row>
        <Row className='row'>
          <Col span={24}>
            <Button onClick={handleCreateGame} id='createGameButton' size='large' type='primary'>Start New Game</Button>
          </Col>
        </Row>
      </div>
    </div>
  )
}
