import React, { useCallback } from 'react'
import { useParams } from 'react-router-dom';
import {
  Button,
  Row,
  Col,
  message
} from 'antd';
import { useNavigate } from 'react-router-dom';

import { useSocket } from 'src/providers/SocketProvider';
import Card from 'src/components/Card';
import Header from 'src/components/Header';
import Result from 'src/components/Result';
import { Player } from 'src/types';

import './styles.scss';

const defaultPlayer = {
  name: 'Anonymous',
  point: '',
}

export default function PlanningBoard(props: any) {
  const [playerList, setPlayerList] = React.useState<Player[]>([]);
  const [cardOpen, setCardOpen] = React.useState<boolean>(false);
  const [gameName, setGameName] = React.useState<string>('');
  const [votingSystem, setVotingSystem] = React.useState<any>('');
  const [cardValue, setCardValue] = React.useState<any>('');
  const [average, setAverage] = React.useState<number>(0);
  const { roomId } = useParams();
  const navigate = useNavigate();

  const socket = useSocket();

  React.useEffect(() => {
    const player = JSON.parse(localStorage.getItem('player') || '{}');
    if (player.socketId) {
      socket.ws.emit('JOIN_ROOM', { roomId: roomId, player: {...player, point: ''} });
    } else {
      socket.ws.emit('JOIN_ROOM', { roomId: roomId, player: defaultPlayer });
    }
    socket.ws.on('ROOM_NOT_FOUND', () => {
      navigate('/not-found')
    })
    socket.ws.on('REFRESH_ROOM', (data) => {
      data.players.forEach((o: Player) => {
        if (o.socketId === socket.ws.id) {
          setCardValue(o.point);
          localStorage.setItem('player', JSON.stringify(o));
        }
      })
      setPlayerList(data.players || []);
      setCardOpen(data.openCard || false);
      setVotingSystem(data.votingSystem);
      setAverage(data.average);
      setGameName(data.gameName || props.gameName);
    })
    socket.ws.on('CHANGE_PLAYER_NAME_SUCCESSFULLY', (data) => {
      setPlayerList(data.players || []);
    })
    socket.ws.on('CLIENT_DISCONNECTED', (data) => {
      setPlayerList(data.players || []);
    });
    // TODO: Reconnect when server is down
    // socket.ws.on('disconnect', () => {
    //   // console.log('Disconnect')
    //   // socket.ws.emit('JOIN_ROOM', { roomId: roomId, player: {...player } });
    // });
    window.addEventListener('offline', (e) => { 
      message.warn('Your connection is unstable!');
    });

    window.addEventListener('online', (e) => { 
      const player = JSON.parse(localStorage.getItem('player') || '{}');
      socket.ws.emit('OUT_ROOM', { roomId: roomId, player: {...player } });
      message.info('Reconnecting...');
      setTimeout(( )=> {
        socket.ws.emit('JOIN_ROOM', { roomId: roomId, player: {...player } });
      }, 2000); 
    });
  }, []);

  const handleChooseCardNumber = (data: any) => {
    const player = JSON.parse(localStorage.getItem('player') || '{}');
    socket.ws.emit('CHOOSE_CARD_NUMBER', { id: player.id, point: data.value });
    setCardValue(data.value);
  }

  const handleRevealCard = () => {
    socket.ws.emit('REVEAL_CARD');
  }

  const handleStartNewVoting = () => {
    socket.ws.emit('START_NEW_VOTING');
  }

  const renderResult = useCallback(()=>{
    return <Result average={average} />
  }, [cardOpen, average]);

  return (
    <>
      <Header showHeaderRight={true} title={gameName} />
      <Row className='row'>
        <Col span={24}>
          <div className='wrapperPlanningBoard'>
            <div className='wrapperPokerTable'>
              <div className='tablePlayerTop'>
                {
                  playerList?.map((p) => {
                    return p.position === "TOP" ? <Card key={p.id} open={cardOpen} player={p} /> : null;
                  })
                }
              </div>
              <div className='tablePlayerBottom'>
                {
                  playerList?.map((p) => {
                    return p.position === "BOTTOM" ? <Card key={p.id} open={cardOpen} player={p} /> : null;
                  })
                }
              </div>

              <div className='tablePlayerLeft'>
                {
                  playerList?.map((p) => {
                    return p.position === "LEFT" ? <Card key={p.id} open={cardOpen} player={p} /> : null;
                  })
                }
              </div>
              <div className='tablePlayerRight'>
                {
                  playerList?.map((p) => {
                    return p.position === "RIGHT" ? <Card key={p.id} open={cardOpen} player={p} /> : null;
                  })
                }
              </div>
              <div className='pokerTable'>
                {cardOpen ?
                  <Button onClick={handleStartNewVoting} type='ghost' size='large'>Start new voting</Button> :
                  <Button onClick={handleRevealCard} type='primary' size='large'>Reveal card</Button>
                }
              </div>
            </div>


          </div>
        </Col>
      </Row>
      {
        cardOpen ? renderResult() : (
          <div className='wrapperChooseCard'>
            {
              <>
                <Row>
                  <Col span={24}>
                    <div className='chooseCardText'>
                      Choose your card 👋
                    </div>
                  </Col></Row>
                <br />
                <Row>
                  <Col span={24}>
                    <div className='cardNumber'>
                      {
                        votingSystem?.split(',')?.map((o: any) => {
                          return <Card key={o} hoverAble={true} active={cardValue == o} onClick={handleChooseCardNumber} open={true} value={o} />
                        })
                      }
                    </div>
                  </Col></Row>
              </>
            }
          </div>
        )

      }

    </>
  )
}

PlanningBoard.defaultProps = {
  gameName : 'We Are Planning'
}