import React from 'react'
import classNames from 'classnames';
import { CloseOutlined } from '@ant-design/icons';
import { CardProps } from 'src/types';

import './styles.scss';

export default function Card(props: CardProps) {
    const { open, value, bottomText, player, active, hoverAble, onClick } = props;
    const handleOnClick = () => {
        onClick({value});
    }
    const renderPoint = () => {
        if (open && !(value || player?.point)){
            return <CloseOutlined style={{color:'#ff4d4f'}} />
        }
        return open && (value || player?.point);
    }
    return (
        <div onClick={handleOnClick} className='wrapperCard'>
            <div className={classNames(open ? 'cardOpen' : '', !!player?.point? 'card': 'cardBlank', active ? 'activeCard': '', hoverAble?'cardHover':'', 'noSelect')}>
                {
                    renderPoint()
                }
            </div>
            {
                !!bottomText || player?.name ? <div className='bottomText'>
                    {bottomText || player?.name}
                </div> : null
            }
        </div>
    )
}

Card.defaultProps = {
    open: false,
    bottomText: '',
    active: false,
    onClick: () => {}
}
